<template>
<div class="login">
  <div class="login-form" @keyup.enter="login">
    <h1>{{ msg }}</h1>
    <el-form :model="formData" :rules="loginRules" ref="loginForm" label-width="80px">
      <el-form-item label="" prop="name">
        <el-input v-model.trim="formData.name" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model.trim="formData.password" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="captcha">
        <el-input v-model.trim="captcha" placeholder="" clearable>
            <template slot="append"><div class="captcha" v-html="captchaImg" @click="getCaptcha"></div></template>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login" class="login-in"></el-button>
        <span>?</span>
        <el-button type="text" @click="()=>{this.$router.push('/register')}"></el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>

<script>
export default {
  name: 'login',
  data () {
    let validateCaptcha = (rule, value, callback) => {
      if (this.captcha === '') {
        callback(new Error(''))
      } else if (this.captcha.toLowerCase() !== this.captchaText) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      msg: '',
      formData: {
        name: '',
        password: ''
      },
      loginRules: {
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        captcha: [{
          required: true,
          validator: validateCaptcha,
          trigger: 'blur'
        }]
      },
      captcha: '',
      captchaImg: '',
      captchaText: ''
    }
  },
  created () {
    if (sessionStorage.getItem('name')) {
      this.formData.name = sessionStorage.getItem('name')
    }
  },
  mounted () {
    this.getCaptcha()
    // if (document.domain !== 'localhost' && document.domain !== 'localhost:3000') {
    //   window.location.href = 'http://localhost:4000'
    // }
  },
  methods: {
    login () {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.$nextTick(() => {
            this.$http.postForm(this.ROOTSERVER +'/back_manage/api/login',this.formData).then(res => {
              if (res.result === 1) {
                this.$message.success(res.msg)
                sessionStorage.setItem('name', this.formData.name)
                this.$router.push('/home')
              } else if (res.result === 2) {
                this.$message.warning(res.msg)
                this.getCaptcha()
              } else {
                this.$message.error('')
                this.getCaptcha()
              }
            }).catch(() => {
              this.$message.error('')
              this.getCaptcha()
            })
          })
        } else {
          return false
        }
      })
    },
    getCaptcha () {
      this.$http.get(this.ROOTSERVER+'/back_manage/api/captcha').then(res => {
        if (res.result === 1) {
          this.captchaImg = res.data
          this.captchaText = res.text.toLowerCase()
        }
      })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
.captcha{
  height:38px;
  overflow:hidden;
  cursor:pointer;
}
</style>
