<template>
  <div class="container">
    <el-form ref="form" :model="submitItem" :rules="itemFormRules" label-width="80px">
      <el-form-item label="" prop="title">
        <div v-text="item.title" v-if="!editStatus"></div>
        <el-input v-else v-model.trim="submitItem.title" placeholder="20" maxlength="20"></el-input>
      </el-form-item>
      <el-form-item label="" prop="classify">
        <div v-text="item.classify" v-if="!editStatus"></div>
        <el-select v-else v-model.trim="submitItem.classify" multiple placeholder="" filterable style="width:100%">
          <el-option
            v-for="child in classifyList"
            :key="child.id"
            :value="child.id"
            :label="child.name"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <template v-if="!editStatus">
          <img :src="ROOTSERVER+item.face_img" style="max-height:200px;" v-if="item.face_img" />
          <span v-else></span>
        </template>
        <template v-else>
          <img :src="ROOTSERVER+submitItem.face_img"  style="max-height:200px;" v-if="submitItem.face_img" />
          <el-upload
            ref="upload"
            :action="ROOTSERVER+'/back_manage/api/upload_img'"
            :with-credentials="true"
            :before-upload="beforeUpload"
            :on-success="successUpload"
            class="upload-item">
            <el-button type="primary" size="small"></el-button>
          </el-upload>
        </template>
      </el-form-item>
      <el-form-item label="" prop="content" class="article-content">
        <div class="markdown-article" v-html="translateMarkdown(item.content)" v-if="!editStatus"></div>
        <mavon-editor v-else ref="md" fontSize="14px" codeStyle="atom-one-dark" @imgAdd="uploadImg" v-model="submitItem.content" />
      </el-form-item>
      <el-form-item>
        <div v-if="editStatus">
          <el-button type="primary" size="small" @click="saveItem"></el-button>
          <el-button size="small" @click="editStatus=!editStatus"></el-button>
        </div>
        <div v-else>
          <el-button type="primary" size="small" @click="editItem"></el-button>
          <el-button size="small" @click="()=>{this.$router.push('/articleManage')}"></el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import businessData from '@/assets/businessData';
import marked from 'marked';
import hljs from 'highlight.js';
export default {
  name: 'articleDetail',
  data () {
    return {
      item: {},
      editStatus: false,
      submitItem: {},
      classifyList:businessData.classifyList,
      itemFormRules:{
        title: [{required:true,message:''}],
        classify: [{required:true,message:''}],
        content: [{required:true,message:''}],
      }
    }
  },
  created () {
    this.getItem()
  },
  methods: {
    getItem () {
      this.$http.get(this.ROOTSERVER+'/back_manage/api/article/detail?id=' + this.$route.query.id).then(res => {
      if (res.result === 1) {
          this.item = res.data
        } else {
          this.$message.error('')
        }
      }).catch(() => {
        this.$message.error('')
      })
    },
    translateMarkdown(content){
      if(content){
        return marked(content, {
          renderer: new marked.Renderer(),
          highlight: function(code) {
              return hljs.highlightAuto(code).value;
          },
          pedantic: false,
          gfm: true,
          tables: true,
          breaks: true,
          sanitize: false,
          smartLists: true,
          smartypants: true
        })
      }else{
        return ''
      }
    },
    editItem () {
      this.editStatus = true
      const classify = this.item.classify.split(',')
      this.submitItem = Object.assign({face_img:''}, this.item,{classify})
    },
    beforeUpload (file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error('JPG/PNG!')
      }
      if (!isLt2M) {
        this.$message.error('2MB!')
      }
      return isJPG && isLt2M
    },
    successUpload (res) {
      if (res.result === 0 ){
        this.$alert('', '', {
          type: 'warning',
          callback: () => {
            this.$router.push('/')
          }
        })
      }else if (res.result === 1) {
        this.$message.success('')
        this.submitItem.face_img = res.url
      }else{
        this.$message.error(res.msg||'')
      }
      this.$refs.upload.clearFiles()
    },
    uploadImg (name, file) {
      this.$http.postFile(this.ROOTSERVER+ '/back_manage/api/upload_img', {file}).then(res => {
        if (res.result === 1) {
          this.$message.success('')
          const $vm = this.$refs['md']
          $vm.$img2Url(name, res.url)
        } else {
          this.$message.error('')
        }
      }).catch(() => {
        this.$message.error('')
      })
    },
    saveItem () {
      this.$refs.form.validate(valid=>{
        if(valid){
          const submitItem = Object.assign({},this.submitItem,{classify:this.submitItem.classify?this.submitItem.classify.join(','):''})
          this.$http.postForm(this.ROOTSERVER+'/back_manage/api/article/update', submitItem).then(res => {
            if (res.result === 1) {
              this.$message.success('')
              this.getItem()
              this.editStatus = false
            }
          }).catch(() => {
            this.$message.error('')
          })
        }
      })
    }
  }
}
</script>
