<template>
  <div class="container">
    <el-form ref="form" :model="itemForm" :rules="itemFormRules" label-width="80px">
      <el-form-item label="" prop="title">
        <el-input v-model.trim="itemForm.title" placeholder="20" maxlength="20" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="classify">
        <el-select v-model.trim="itemForm.classify" multiple placeholder="" filterable style="width:100%">
          <el-option
            v-for="item in classifyList"
            :key="item.id"
            :value="item.id"
            :label="item.name"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <img :src="ROOTSERVER+itemForm.face_img" v-if="itemForm.face_img" style="max-height:200px;" />
        <el-upload
            ref="upload"
            :action="ROOTSERVER+'/back_manage/api/upload_img'"
            :with-credentials="true"
            :before-upload="beforeUpload"
            :on-success="successUpload"
            class="upload-item">
            <el-button type="primary" size="small"></el-button>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="content" class="article-content">
        <mavon-editor ref="md" fontSize="14px" codeStyle="atom-one-dark" @imgAdd="uploadImg" v-model.trim="itemForm.content" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" @click="addItem"></el-button>
        <el-button size="small" @click="()=>{this.$router.push('/articleManage')}"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import businessData from '@/assets/businessData';
export default {
  name: 'newArticle',
  data () {
    return {
      itemForm: {
        title: '',
        classify: '',
        face_img:'',
        content: '',
      },
      itemFormRules:{
        title: [{required:true,message:''}],
        classify: [{required:true,message:''}],
        content: [{required:true,message:''}],
      },
      classifyList:businessData.classifyList
    }
  },
  methods: {
    addItem () {
      this.$refs.form.validate(valid=>{
        if(valid){
          const itemForm = Object.assign({},this.itemForm,{classify:this.itemForm.classify?this.itemForm.classify.join(','):''}) 
          this.$http.postForm(this.ROOTSERVER + '/back_manage/api/article/new', itemForm).then(res => {
            if (res.result === 1) {
              this.$message.success('')
              this.$router.push({path: '/articleDetail', query: {id: res.data._id}})
            } else {
              this.$message.error('')
            }
          }).catch(() => {
            this.$message.error('')
          })
        }
      })
    },
    beforeUpload (file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error('JPG/PNG!')
      }
      if (!isLt2M) {
        this.$message.error('2MB!')
      }
      return isJPG && isLt2M
    },
    successUpload (res) {
      if (res.result === 0 ){
        this.$alert('', '', {
          type: 'warning',
          callback: () => {
            this.$router.push('/')
          }
        })
      }else if (res.result === 1) {
        this.$message.success('')
        this.itemForm.face_img = res.url
      }
      this.$refs.upload.clearFiles()
    },
    uploadImg (name, file) {
      this.$http.postFile(this.ROOTSERVER + '/back_manage/api/upload_img', {file}).then(res => {
        if (res.result === 0) {
          this.$alert('', '', {
            type: 'warning',
            callback: () => {
              this.$router.push('/')
            }
          })
        } else if (res.result === 1) {
          this.$message.success('')
          const $vm = this.$refs['md']
          $vm.$img2Url(name, res.url)
        } else {
          this.$message.error('')
        }
      }).catch(() => {
        this.$message.error('')
      })
    }
  }
}
</script>
