import qs from 'qs'
export default {
  install (Vue, { axios, router }) {
    Vue.prototype.$countTime = (time) => {
      let now = new Date()
      time = new Date(time)
      if (now.getFullYear() - time.getFullYear() > 0) {
        return now.getFullYear() - time.getFullYear() + ''
      } else if (now.getMonth() - time.getMonth() > 0) {
        return now.getMonth() - time.getMonth() + ''
      } else if (now.getDate() - time.getDate() > 0) {
        return now.getDate() - time.getDate() + ''
      } else if (now.getHours() - time.getHours() > 0) {
        return now.getHours() - time.getHours() + ''
      } else if (now.getMinutes() - time.getMinutes() > 0) {
        return now.getMinutes() - time.getMinutes() + ''
      } else {
        return now.getSeconds() - time.getSeconds() + ''
      }
    }
    function checkTimeout (data) {
      if (data.result === 0) {
        return Vue.prototype.$alert('', '', {
          type: 'warning',
          callback: () => {
            router.push('/')
          }
        })
      } else {
        return data
      }
    }
    function encodeFormData (data) {
      let formData = new FormData()
      for (let key in data) {
        formData.append(key, data[key])
      }
      return formData
    }

    class HttpRequest {
      get (url) {
        return new Promise((resolve, reject) => {
          axios({
            method: 'get',
            url,
            withCredentials: true
          }).then(res => {
            url.indexOf('back_manage/api/logout') !== -1 ? resolve(checkTimeout(res.data)) : resolve(res.data)
            resolve(checkTimeout(res.data))
          }).catch(err => {
            reject(err)
          })
        })
      }
      postJSON (url, data) {
        return new Promise((resolve, reject) => {
          axios({
            method: 'post',
            url,
            data,
            withCredentials: true,
            headers: { 'Content-Type': 'application/json; charset=utf-8' }
          }).then(res => {
            resolve(checkTimeout(res.data))
          }).catch(err => {
            reject(err)
          })
        })
      }
      postForm (url, data) {
        return new Promise((resolve, reject) => {
          axios({
            method: 'post',
            url,
            data: qs.stringify(data),
            withCredentials: true,
            headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8' }
          }).then(res => {
            resolve(checkTimeout(res.data))
          }).catch(err => {
            reject(err)
          })
        })
      }
      postFile (url, data) {
        return new Promise((resolve, reject) => {
          axios({
            method: 'post',
            url,
            data: encodeFormData(data),
            withCredentials: true,
            headers: { 'Content-Type': 'multipart/form-data' }
          }).then(res => {
            resolve(checkTimeout(res.data))
          }).catch(err => {
            reject(err)
          })
        })
      }
    }
    Vue.prototype.$http = new HttpRequest()
  }
}
