<template>
<div class="login">
  <div class="login-form" @keyup.enter="register">
    <h1>{{ msg }}</h1>
    <el-form :model="checkData" ref="registerForm" :rules="registerRules" label-width="80px">
      <el-form-item label="" prop="name">
        <el-input v-model.trim="checkData.name" autoComplete="off" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model.trim="checkData.password" autoComplete="off" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="confirmPassword">
        <el-input type="password" v-model.trim="confirmPwd" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="login-in" @click="register"></el-button>
        <span>?</span>
        <el-button type="text" @click="()=>{this.$router.push('/')}"></el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>

<script>
export default {
  name: 'regiser',
  data () {
    let validatePassword = (rule, value, callback) => {
      if (this.confirmPwd === '') {
        callback(new Error(''))
      } else if (this.confirmPwd !== this.checkData.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      msg: '',
      checkData: {
        name: '',
        password: ''
      },
      confirmPwd: '',
      registerRules: {
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
        {
          pattern: /^\S{5,20}$/,
          message: '5-20'
        }],
        confirmPassword: [{
          required: true,
          validator: validatePassword,
          trigger: 'blur'
        }]
      }
    }
  },
  methods: {
    register () {
      this.$refs.registerForm.validate((valid) => {
        if (valid) {
          this.$http.postForm(this.ROOTSERVER+'/back_manage/api/register', this.checkData).then(res => {
            if (res.result === 1) {
              this.$message.success('')
              this.$router.push('/')
            } else if (res.result === 2) {
              this.$message.warning(res.msg)
            } else {
              this.$message.error('')
            }
          }).catch(() => {
            this.$message.error('')
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>

